.work-container {
    background-color: #000;
    padding: 50px 0;
  }
  
  .project-heading {
    color: #fff;
    font-size: 36px;
    text-align: center;
    margin-bottom: 50px;
  }
  
  .project-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
  }
  
  .project-card {
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin: 20px;
    width: calc(33.33% - 40px);
    min-height: 350px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
    background-color: #fff;
    transition: box-shadow 0.3s ease;
  }
  
  .project-card:hover {
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  }
  
  .project-card img {
    max-width: 100%;
    max-height: 200px;
    margin-bottom: 10px;
    object-fit: cover;
  }
  
  .project-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
    color: #444;
  }
  
  .pro-details {
    flex-grow: 1;
  }
  
  .pro-details p {
    margin-bottom: 10px;
    color: #777;
    font-size: 16px;
  }
  
  .pro-btns {
    display: flex;
    justify-content: flex-end;
  }
  
  .pro-btns .btn {
    background-color: #007bff;
    color: #fff;
    border-radius: 5px;
    padding: 10px 20px;
    text-decoration: none;
    font-size: 16px;
    transition: background-color 0.3s ease;
  }
  
  .pro-btns .btn:hover {
    background-color: #0069d9;
  }
  