.project-card {
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin: 10px;
  flex-basis: calc(33.33% - 20px);
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  background-color: #000;
}

.project-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #fff;
}

.pro-details {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.pro-details p {
  margin-bottom: 10px;
  flex-grow: 1;
  color: #ddd;
}

.pro-btns {
  display: flex;
  justify-content: flex-end;
}

.pro-btns .btn {
  background-color: #007bff;
  color: #fff;
  border-radius: 5px;
  padding: 10px 20px;
  text-decoration: none;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.pro-btns .btn:hover {
  background-color: #0069d9;
  color: #fff;
}

@media (max-width: 768px) {
  .project-card {
    flex-basis: calc(50% - 20px);
  }
}

@media (max-width: 576px) {
  .project-card {
    flex-basis: 100%;
  }
}

.project-card img {
  max-width: 100%;
  height: 200px;
  margin-bottom: 10px;
  object-fit: cover;
}
