  /* Set a beautiful hero section */
.hero {
  position: relative;
  min-height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url("../assests/background-image.jpg") center center/cover no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
}

/* Create a mask on top of the hero section */
.mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* Style the image */
.my-img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 2rem;
}

/* Set the introduction text style */
.introduction {
  text-align: center;
  margin-bottom: 1.5rem;
  color: #fff;
  font-size: 1.5rem;
  font-weight: bold;
  animation-name: intro-animation;
  animation-duration: 1s;
  animation-fill-mode: both;
}

/* Set the font family for the introduction text */
.intro-text {
  font-family: 'Montserrat', sans-serif;
  color:#F0FFFF;
}

/* Add an animation for the introduction text */
@keyframes intro-animation {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Style the buttons */
.buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;
}

/* Set the button style */
.btn {
  padding: 1rem 2rem;
  border: 2px solid #fff;
  border-radius: 2rem;
  text-decoration: none;
  color: #fff;
  font-size: 1.2rem;
  font-weight: bold;
  transition: background-color 0.3s, border-color 0.3s, color 0.3s;
  background-color: transparent;
}

/* Set the button hover style */
.btn:hover {
  background-color: #fff;
  border-color: #fff;
  color: #111;
}
@media only screen and (max-width: 768px) {
  /* Adjusting image and font size for smaller screens */
  .my-img {
  width: 150px;
  height: 150px;
  }
  
  .introduction {
  font-size: 1.2rem;
  }
  
  .btn {
  font-size: 1rem;
  padding: 0.8rem 1.5rem;
  }
  }
  
  @media only screen and (max-width: 480px) {
  /* Further adjusting image and font size for even smaller screens */
  .my-img {
  width: 120px;
  height: 120px;
  }
  
  .introduction {
  font-size: 1rem;
  }
  
  .btn {
  font-size: 0.8rem;
  padding: 0.6rem 1.2rem;
  }
  }
  
  @media only screen and (max-width: 360px) {
  /* Final adjustments for the smallest screens */
  .my-img {
  width: 90px;
  height: 90px;
  }
  
  .introduction {
  font-size: 0.8rem;
  }
  
  .btn {
  font-size: 0.7rem;
  padding: 0.5rem 1rem;
  }
  }