.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  padding: 1rem;
  height: 80px;
}

.navbar__logo {
  width: 120px;
  height: 80px;
}

.navbar__logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}


.navbar__links {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.navbar__links li {
  margin-left: 1rem;
}

.navbar__links a {
  color: #fff;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: bold;
  transition: color 0.3s;
  padding: 0.5rem;
}

.navbar__links a:hover {
  color: #0077b6;
  background-color: #f7f7f7;
  border-radius: 5px;
}

.navbar__menu {
  display: none;
}

/* Media queries */
@media only screen and (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: stretch;
    padding: 1rem 0;
  }
  .navbar__logo {
    width: 120px;
    height: 80px;
    z-index: 999;
  }  

  .navbar__links {
    flex-direction: column;
    position: absolute;
    top: 80px;
    left: -100%;
    z-index: 999;
    width: 100%;
    height: calc(100vh - 80px);
    background-color: #000;
    transition: all 0.5s ease;
  }

  .navbar__links.active {
    left: 0;
  }

  .navbar__links li {
    margin: 1rem 0;
  }

  .navbar__links a:hover {
    color: #fff;
    background-color: #0077b6;
    border-radius: 5px;
  }

  .navbar__menu {
    display: block;
    position: absolute;
    top: 1rem;
    right: 1rem;
    cursor: pointer;
    transform: translate(-50%, 50%);
    z-index: 999;
  }

  .navbar__menu i {
    font-size: 1.5rem;
    color: #fff;
  }

  .navbar__menu i:hover {
    color: #0077b6;
  }
}
